

















import {
  defineComponent, onMounted, watchEffect,
} from '@vue/composition-api';
import { useTrackingConsent } from '@/app/website/client/logic/tracking-consent';

function changeEcwidTrackingSettings(
  userResponseReceived: boolean,
  trackingConsentEnabled: boolean,
  trackingAllowed: boolean,
): void {
  if (!trackingConsentEnabled) {
    return;
  }

  const trackingResponse = trackingAllowed ? 'ACCEPTED' : 'DECLINED';
  window.ec = window.ec ?? {};
  window.ec.config = window.ec.config ?? {};
  window.ec.config.tracking = window.ec.config.tracking ?? {};
  window.ec.config.tracking.ask_consent = true;
  window.ec.config.tracking.user_response = trackingResponse;

  if (userResponseReceived && window.Ecwid !== undefined) {
    window.Ecwid.setTrackingConsent(trackingResponse);
  }
}

const ProductBrowserTile = defineComponent({
  setup(_, { root }) {
    const trackingConsent = useTrackingConsent(root.$store);

    onMounted(() => {
      watchEffect(() => {
        changeEcwidTrackingSettings(
          trackingConsent.userResponseReceived.value,
          trackingConsent.trackingConsentEnabled.value,
          trackingConsent.trackingAllowed.value,
        );
      });
    });
  },
});

export default ProductBrowserTile;
